body {
    margin: 0;
    padding: 0;
    background-color: #f2f2f2;
    height: 100%;
    font-family: Arial, Helvetica, sans-serif;
}
html,body,.container {
    height: 100%;
    display: flex;
    justify-content: center;
    background-color:  #e1e1ea;
}
.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
.movies {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80%;
    padding-top: 5%;
    padding: 50px;
}

.movies .movie {
    width: 40%;
    padding: 2.5%;
    margin: 2%;
    max-width: 1400px;
    background-color: #f2f2f2;
    font-weight: 100;
    justify-content: space-between;
    margin-bottom: 80px;
    -webkit-box-shadow: 10px 10px 17px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 17px -10px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 17px -10px rgba(0,0,0,0.75);
    border-radius: 5px;
    font-weight: 300;
    color:#adaeb9;
    min-width: 400px;
}

.movie img {
    position: relative;
    -webkit-box-shadow: 10px 10px 17px -10px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 17px -10px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 17px -10px rgba(0,0,0,0.75);
    max-width: 120px;
    top: -50px;
    border-radius: 5px;
    margin-right:30px;
}

.movie .movie_title, .movie .movie_year {
    padding: 10px;
    margin: 0;
    font-weight: 300;

}
.movie .movie_title {
    margin-top: 20px;
    color: rgba(46, 44, 44, 0.89);
}

.movie .movie_summary{
    font-size: 15px;
    padding: 10px;
    margin: 0;
}
.movie .movie_genres {
    padding-left: 10px;
    display: flex;
    flex-wrap: wrap;
    min-width: 100px;
}

.movie .genres_genre {
    padding:0;
    margin: 0;
    list-style: none;
    display: flex;
    font-size: 13px;
    margin-right: 10px;
}

.movie .movie_data {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    margin-left: 150px;
    margin-top: -200px;
    max-width: 500px;
    min-width: 200px;
}